import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class NotificationService {

    getNewNotification() {
        return axios.get(API_URL+'notification/new-customer', { headers: authHeader() });
    }

    getNewNotificationCase() {
        return axios.get(API_URL+'notification/new-case', { headers: authHeader() });
    }

    markAll() {
        return axios.get(API_URL+'notification/mark-all', { headers: authHeader() });
    }

    readCustomers(data){
        return axios.post(API_URL+'notification/read', data, { headers: authHeader() });
    }

    readCases(data){
        return axios.post(API_URL+'notification/read', data, { headers: authHeader() });
    }

    readALL(data){
        return axios.post(API_URL+'notification/read-all', data, { headers: authHeader() });
    }

    unread(id){
        return axios.get(API_URL+'notification/unread/'+id, { headers: authHeader() });
    }


}

export default new NotificationService();
