<script>
import i18n from "../i18n";
import simplebar from "simplebar-vue";
import { Store } from 'vuex';
import store from '@/state/store';
import UserService from "@/helpers/user.service";
import NotificationService from '@/helpers/notification.service'
import moment from "moment";
/**
 * Nav-bar Component
 */
export default {
  data() {
    return {
      sidebar: true,
      lang: '',
      user: '',
      notif_total: '',
      notif_total_case:'',
      notif:[],
      notif_case:[],
      users: store.state.authBackend.user,
      urlImage: process.env.VUE_APP_BASE_API_URL_AVATAR,
      avatar: '',
      notif_customer:'',
      notif_cases:'',
      // avatar: JSON.parse(localStorage.getItem('user')).avatar,
      languages: [
        {
          flag: require("@/assets/images/flags/en.png"),
          language: "en",
          title: "English"
        },
        {
          flag: require("@/assets/images/flags/ina.png"),
          language: "id",
          title: "Indonesian"
        }
      ]
    };
  },
  components: { simplebar },
  props: {
    UserNotif : Number,
    CaseNotif : Number,
  },

  methods: {
    list() {
      UserService.getProfile()
      .then(response => {
        this.user = response.data.data,
        console.log(response.data.data);
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    toggleMenu() {
      if(this.sidebar){
        this.sidebar = false;
      } else {
         this.sidebar = true;
      }
      this.$parent.toggleMenu();
    },
    toggleRightSidebar() {
      this.$parent.toggleRightSidebar();
    },
    initFullScreen() {
      document.body.classList.toggle("fullscreen-enable");
      if (
        !document.fullscreenElement &&
        /* alternative standard method */ !document.mozFullScreenElement &&
        !document.webkitFullscreenElement
      ) {
        // current working methods
        if (document.documentElement.requestFullscreen) {
          document.documentElement.requestFullscreen();
        } else if (document.documentElement.mozRequestFullScreen) {
          document.documentElement.mozRequestFullScreen();
        } else if (document.documentElement.webkitRequestFullscreen) {
          document.documentElement.webkitRequestFullscreen(
            Element.ALLOW_KEYBOARD_INPUT
          );
        }
      } else {
        if (document.cancelFullScreen) {
          document.cancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        }
      }
    },
    setLanguage(locale) {
      localStorage.setItem('lang', locale);
      location.reload();
    },
    getNotif() {
      // this.loading = true;
      NotificationService.getNewNotification()
        .then((response) => {
          this.notif = [];
          this.notif_total = response.data.data.length;
          response.data.data.forEach((val) => {
            const data = {
              id: val.id,
              type: val.type,
              notifiale_type: val.notifiale_type,
              notifiable_id: val.notifiable_id,
              data: val.data,
              read_at: val.read_at,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.notif.push(data);
          });
          //  this.loading = false;
        })
        .catch((err) => {
          console.log(err);
          // this.loading = false;
        });
    },
    getNotifCase() {
      NotificationService.getNewNotificationCase()
        .then((response) => {
          this.notif_case = [];
          this.notif_total_case = response.data.data.length;
          response.data.data.forEach((val) => {
            const data = {
              id: val.id,
              type: val.type,
              notifiale_type: val.notifiale_type,
              notifiable_id: val.notifiable_id,
              data: val.data,
              read_at: val.read_at,
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.notif_case.push(data);
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    formatDate(value) {
      return moment(value).format("YYYY-MM-DD, hh:mm");
    },

    readCustomer(id, id_user){
      let data = {
         "notif" : "customer",
         "id_notif" : id
      }
      console.log(data);
        NotificationService.readCustomers(data)
        .then((response) => {
         location.replace('/customer-management-detail/'+id_user);
        //  this.getNotif();
        })
        .catch((err) => {
          console.log(err);
        });
    },

    readCase(id, id_case){
      let data = {
         "notif" : "case",
         "id_notif" : id
      }
      console.log(data);
        NotificationService.readCases(data)
        .then((response) => {
         location.replace('/case-management-detail/'+id_case);
        //  this.getNotifCase();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  watch: {
    UserNotif: {
      handler: function(){
        this.getNotif();
        // console.log('user');
      },
      deep:true
    },
     CaseNotif: {
      handler: function(){
        this.getNotifCase();
        // console.log('case');
      },
      deep:true
    },
  },
  beforeMount(){
    this.list();
  },
  mounted(){
    // window.Echo.channel('notif').listen('NotifCreated', (event) => {
    //   console.log('Berhasil Listen', event);
    //    this.getNotif();
    //    this.getNotifCase();

    // })
    this.getNotif();
    this.getNotifCase();
    window.addEventListener('ava-changed', (event) => {
      this.avatar = event.detail.avatar;
    });
    this.avatar = JSON.parse(localStorage.getItem('user')).avatar;
    this.lang =  localStorage.getItem('lang');
  }
};
</script>